export default {
	props: {
		show: {
			type: Boolean,
			default: false
		},
		top: {
			type: Number,
			default: 0
		},
		articleId: {
			type: Number,
			default: 0
		},
		userId: {
			type: Number,
			default: 0
		},
		index: {
			type: Number,
			default: 0
		}
	},
	data() {
		let offsetTop = 0;



		return {};
	},
	methods: {
		hide() {
			this.$emit('hideArticleOperate');
		},
		shield(userId) {
			console.log(userId)
			this.api.home.article.toggle_shield({
				vid: userId,
				action: 1
			}, res => {
				this.hide();
				this.$emit('refreshList');
				uni.showToast({
					title:"屏蔽成功",
				})
			})
		},
		unInterest(articleId, index) {
			console.log(articleId)
			this.api.home.article.un_interest({
				articleId
			}, res => {
				this.hide();
				this.$emit('removeArticle', index);
				uni.showToast({
					title:"谢谢您的反馈",
					icon:"none"
				})
			})
		}
	},

};
